export default {
  name: 'BliGeneralLoader',
  props: {
    inverted: Boolean,
    size: {
      type: Number,
      default: 24
    }
  },
  computed: {
    firstStroke () {
      if (this.inverted) return "#E0E0E0"
      return "#F1F1F1"
    },
    secondStroke () {
      if (this.inverted) return "#FFFFFF"
      return "#0095DA"
    },
  }
}
