import SharedButton  from "../../../../shared/js/shared-button"
import BliGeneralLoader from '../../../../shared/assets/general-loader/GeneralLoader.vue'
import RandomId from '@blibli/blue-dls-utils/dist/js/random-id'

export default {
  props: {
    prominence: {
      type: String,
      default: 'primary'
    },
    fullWidth: Boolean,
    loading: Boolean,
    id: {
      type: String,
      default: () => 'bli-button-' + RandomId()
    }
  },
  components: {
    BliGeneralLoader
  },
  mixins: [SharedButton],
  computed: {
    buttonClasses () {
      return {
        'b-full-width': this.fullWidth,
        'b-has-icon-only': this.hasIcon && this.hasEmptyContent,
        'b-loading': this.loading
      }
    }
  }
}